<template>
  <div class="releaseSale">
    <div class="head"></div>
    <form class="saleForm">
      <div :class="{warnActive:showMsgModel.name}">
        <label>标题*</label>
        <input type="text" v-model="saleModle.name" v-on:blur="checkName" v-on:input="checkName"></input>
        <span v-if="showMsgModel.name">标题不能少于5个字符</span>
      </div>
      <div :class="{warnActive:showMsgModel.price}">
        <label>价格*</label>
        <input type="text" v-model="saleModle.price" v-on:blur="checkNumber(saleModle.price,'price')"></input>
        <span v-if="showMsgModel.price">价格必须为整数</span>
      </div>
      <div>
        <label>分类</label>
        <select ref="select">
          <option>代步工具</option>
          <option>手机</option>
          <option>电脑</option>
          <option>数码</option>
          <option>电器</option>
          <option>书籍</option>
          <option>衣物</option>
          <option>体育健身</option>
          <option>乐器</option>
          <option>其他</option>
        </select>
      </div>
      <div :class="{warnActive:showMsgModel.place}">
        <label>交易地点</label>
        <input type="text" v-model="saleModle.place" v-on:blur="checkEmpty(saleModle.place,'place')"></input>
        <span v-if="showMsgModel.place">请填写交易地点</span>
      </div>
      <div :class="{warnActive:showMsgModel.tel}">
        <label>手机</label>
        <input type="text" v-model="saleModle.tel" v-on:blur="checkNumber(saleModle.tel,'tel')"></input>
        <span v-if="showMsgModel.tel">手机必须为数字</span>
      </div>
      <div :class="{warnActive:showMsgModel.qq}">
        <label>QQ</label>
        <input type="text" v-model="saleModle.qq" v-on:blur="checkNumber(saleModle.qq,'qq')"></input>
        <span v-if="showMsgModel.qq">QQ必须为数字</span>
      </div>
      <div :class="{warnActive:showMsgModel.description}">
        <label>详情介绍</label>
        <textarea v-model="saleModle.description"  v-on:blur="checkEmpty(saleModle.description,'description')"></textarea>
        <span v-if="showMsgModel.descrition">请填写详细介绍</span>
      </div>
      <div>
      <label>上传图片</label>
      <input  @change="onFileChange" value="上传" type="file"></input>
      </div>
      <div class="imgWrapper">
        <img :src="dataUrl" class="preImg" id="firstImg" />
        <img :src="imgData[0]" class="preImg"/>
        <img :src="imgData[1]" class="preImg"/>
        <img :src="imgData[2]" class="preImg"/>
        <img :src="imgData[3]" class="preImg"/>
      </div>
      <div>
        <input class="subButton" type="button" @click="submmitForm" value="提交"></button>
      </div>

    </form>
  </div>
</template>

<script>
import { bus } from '../../assets/bus.js';
export default {
  created(){
    this.checkLogin();
  },
  watch:{
    "$route": "checkLogin"
  },
  data(){
    return {
      userModel:{},
      saleUrl:"/dachuang/releaseSale",
      saleModle:{
        "name":"",
        "price":"",
        "sort":"",
        "tel":"",
        "qq":"",
        "place":"",
        "description":"",
        "userID":"",
        "img":""
      },
      showMsgModel:{
        "name":false,
        "price":false,
        "tel":false,
        "qq":false,
        "place":false,
        "description":false
      },
      success:{
        "name":false,
        "price":false,
        "tel":false,
        "qq":false,
        "place":false,
        "description":false
      },
      imgData:["","","",""],
      dataUrl:"",
      currentImg:0,
      loginUrl:"/dachuang/isStatus"
    }
  },
  methods:{
    checkLogin:function(){
      var vm=this;
      vm.$http.get(vm.loginUrl
              ).then(function(res){
                if(res.body.code==0){
                  console.log("用户未登录");
                  localStorage.user="";
                  this.$router.push("/login");
                }
              },function(err){
                console.log(err);
                this.$router.push("/login");
              })
    },
    checkForm:function(obj){
      console.log(this.success)
      for(var key in obj){   
        if (obj.hasOwnProperty(key) === true){
          if(!obj[key]){
            return false; 
          } 
        }                 
      }  
      if(!this.dataUrl){
        alert("请至少上传一张图片作为封面");
        return false;
      }else{
        for(var i=0;i<this.imgData.length;i++){
          if(this.imgData[i]){         
            this.saleModle.img=this.saleModle.img+this.delDataUrlHead(this.imgData[i]);
            console.log(this.saleModle.img)
             console.log(this.imgData)
          }         
        }
        this.dataUrl=this.delDataUrlHead(this.dataUrl);
        
      }
      this.saleModle.sort=this.$refs.select.selectedIndex+1;
      return true;
    },
    submmitForm:function(){
      var vm=this;
      console.log(vm.saleModle)
      if(this.checkForm(this.success)){
        vm.$http.post(vm.saleUrl,vm.saleModle,{
                        emulateJSON:true
                      }).then(function(res){
                         console.log(res.body);
                        
                      })
      }
    },
    checkEmpty:function(val,key){     
      if(!val){
        this.showMsgModel[key]=true;
        this.success[key]=false;
      }else{
        this.showMsgModel[key]=false;
        this.success[key]=true;
      }
    },
    checkNumber:function(val,key){

      var reg=/^[0-9]*$/;
      if(!reg.test(val)){
        this.showMsgModel[key]=true;
        this.success[key]=false;
      }else{
        this.showMsgModel[key]=false;
        this.success[key]=true;
      }

    },
    checkName:function(){
      var len=this.saleModle.name.length;
      if(len<5){
        this.showMsgModel.name=true;
        this.success.name=false;
      }else{
        this.showMsgModel.name=false;
        this.success.name=true;
      }
    },
    onFileChange:function(e) {
      var files = e.target.files || e.dataTransfer.files;
      console.log(files);
      if (!files.length||this.currentImg>=4) return;

      var size=Math.floor(files[0].size/1024/1024);
      console.log(size);
      if(size>10) return false;

      this.createImage(files[0]);
    },
    createImage:function(file) {
    var image = new Image();
    var reader = new FileReader();
    var vm = this;
    
    reader.onloadend = function () {
       vm.dataUrl=this.result;
        var index=vm.currentImg;
        vm.imgData[index]=this.result;
        vm.currentImg++;
    }
    reader.readAsDataURL(file);
   },
   delDataUrlHead:function(data){

      var index=data.indexOf(',')+1;
      var result=data.substring(index);
      return result;

  },
  }
}
</script>

<style scoped>
  .head{
    width: 70%;
    height: 50px;
    margin: 20px;
    display: inline-block;
    border-radius: 20px 20px 0px 0px;
    background-color: rgba(253, 208, 72, 0.847058823);
  }
  .releaseSale{
    padding-top:200px;

  }
  .saleForm{
    text-align: left;
    
  }
  .saleForm span{
    width: 70%;
    height: 20px;
    position: absolute;
    display: inline-block;
    text-align: left;
    color: red;
    font-size: 10px;
  }
  .saleForm label{
    margin-right: 20px;
    font-weight: 700;
    font-size: 10px;
    vertical-align: middle;
  }
  .saleForm div{
    width: 80%;
    margin: 15px;
    display: inline-block;
    text-align: right;

  }
  .saleForm input{
    width: 70%;
    height: 40px;
    display: inline-block;
    border-radius: 2px;
    border: 1px solid rgba(0,0,0,0.2);
    color:rgba(0,0,0,0.6);
  }
  .saleForm select{
    width: 70%;
    height: 40px;
    display: inline-block;
    border-radius: 2px;
    border: 1px solid rgba(0,0,0,0.2);
    color:rgba(0,0,0,0.6);
  }
  .saleForm textarea{
    width: 70%;
    height: 70px;
    display: inline-block;
    border-radius: 2px;
    border: 1px solid rgba(0,0,0,0.2);
    color:rgba(0,0,0,0.6);
  }
  .subButton{
    height: 50px;
    color: #fff;
    background: inherit;
    background-color: rgba(253, 208, 72, 0.847058823529412);
    border: none;
    color: #fff;
   
  }
  .subButton:hover{
    cursor: pointer;
  }
  .warnActive{
    color: red;
  }
  input:focus{
    box-shadow: 1px 1px 5px #58bace;
  }
  .preImg{
    width: 150px;
    height: 150px;
    margin-right: 20px;
    display: inline-block;
    border: none;
    background: rgba(0,0,0,0.2);
    border-radius: 10px;
  }
  #firstImg{
    display: none;
  }
</style>


